import "./style.css"
import { logout } from "components/authentication/v1/actions"
import { Provider, connect } from "react-redux"
import { Link } from "react-router-dom"
import {
	Button,
	Container,
	Divider,
	Dropdown,
	Grid,
	Icon,
	Image,
	Input,
	Menu,
	Responsive,
	Sidebar
} from "semantic-ui-react"
import fallacies from "fallacies.json"
import ImagePic from "images/avatar/brain-fart.gif"
import LoadingBar from "react-redux-loading-bar"
import Logo from "./images/logo.svg"
import NavSearch from "components/search/v1/"
import PropTypes from "prop-types"
import React, { Component } from "react"
import ReactSVG from "react-svg"
import store from "store"
import Url from "url-parse"

class Header extends Component {
	constructor(props) {
		super(props)
		this.state = {
			activeItem: "",
			value: "",
			visible: false
		}

		this.onLogout = this.onLogout.bind(this)
	}

	handleItemClick = (key, name) => {
		this.setState({ activeItem: name, visible: false })
		this.props.history.push(`/fallacies/${key}`)
	}

	onChangeSearch = (e, { value }) => this.setState({ value })

	onLogout() {
		this.props.logout()
		const parsed = new Url(window.location)
		if (parsed.pathname !== "/") {
			window.location.reload()
		}
	}

	render() {
		const { activeItem, value, visible } = this.state

		const FallaciesSidebar = fallacies.map(fallacy => (
			<Menu.Item
				active={activeItem === fallacy.name.toLowerCase()}
				key={fallacy.id}
				name={fallacy.name.toLowerCase()}
				onClick={() => this.handleItemClick(fallacy.key, fallacy.name)}
			>
				{`${fallacy.name}`}
			</Menu.Item>
		))

		const LoginButton = props => {
			if (props.authenticated) {
				const trigger = (
					<Image
						avatar
						bordered
						circular
						onError={i => (i.target.src = ImagePic)}
						rounded
						src={props.data.img ? props.data.img : ImagePic}
					/>
				)
				return (
					<Menu.Item position="right">
						<Dropdown
							className="dropDownMenu"
							icon={false}
							pointing="top right"
							trigger={trigger}
						>
							<Dropdown.Menu>
								<Dropdown.Item
									onClick={() =>
										props.history.push(`/users/${props.data.username}`)
									}
									text={props.data.name}
								/>
								<Dropdown.Divider />
								<Dropdown.Item
									onClick={() =>
										props.history.push(
											`/users/${props.data.username}/fallacies`
										)
									}
									text="My Fallacies"
								/>
								<Dropdown.Item
									onClick={() =>
										props.history.push(`/users/${props.data.username}/archives`)
									}
									text="My Archives"
								/>
								<Dropdown.Divider />
								<Dropdown.Item
									onClick={() => props.history.push(`/settings`)}
									text="Settings"
								/>
								<Dropdown.Item onClick={this.onLogout} text="Log out" />
							</Dropdown.Menu>
						</Dropdown>
					</Menu.Item>
				)
			} else {
				return (
					<Menu.Item className="signInLink" direction="right" position="right">
						<Grid columns={2} relaxed="very">
							<Grid.Column>
								<Link to="/signin?type=signin">Sign In</Link>
							</Grid.Column>
							<Grid.Column>
								<Button
									color="green"
									content="Join"
									onClick={() => props.history.push("/signin?type=join")}
								/>
							</Grid.Column>
						</Grid>
						<Divider fitted vertical>
							or
						</Divider>
					</Menu.Item>
				)
			}
		}

		return (
			<Provider store={store}>
				<div className="pageHeader">
					<Menu borderless className="globalHeader" fitted="vertically" fixed="top">
						<Container className="headerContainer">
							<Responsive className="responsive" maxWidth={1024}>
								<Menu.Item className="headerMenuItem">
									<ReactSVG
										className="headerLogo"
										evalScripts="always"
										onClick={() => {
											this.props.history.push("/activity")
										}}
										src={Logo}
										svgClassName="svgHeaderLogo"
									/>
								</Menu.Item>
								<Menu.Item className="sidebarItem" position="right">
									<Icon
										name="sidebar"
										onClick={() =>
											this.setState({
												visible: !visible
											})
										}
										size="large"
									/>
								</Menu.Item>
							</Responsive>
							<Responsive className="responsive" minWidth={1025}>
								<Menu.Item className="headerMenuItem">
									<ReactSVG
										className="headerLogo"
										evalScripts="always"
										onClick={() => {
											this.props.history.push("/activity")
										}}
										src={Logo}
										svgClassName="svgHeaderLogo"
									/>
									<NavSearch history={this.props.history} />
								</Menu.Item>
								<Menu.Item className="fallaciesLink">
									<Link to="/assign">
										<Icon color="green" name="plus" /> Assign
									</Link>
								</Menu.Item>
								<Menu.Item className="fallaciesLink">
									<Link to="/fallacies">
										<Icon color="blue" name="book" /> Reference
									</Link>
								</Menu.Item>
								{LoginButton(this.props)}
							</Responsive>
						</Container>
					</Menu>
					<LoadingBar className="loadingBar" />
					<Sidebar
						as={Menu}
						animation="overlay"
						icon="labeled"
						vertical
						visible={visible}
						width="wide"
					>
						<Menu.Item as="div" name="search">
							<Input
								className="sidebarSearch"
								onChange={this.onChangeSearch}
								placeholder="Search..."
								value={value}
							/>
							<Button
								className="sidebarSearchBtn"
								color="blue"
								content="Search"
								fluid
								icon="search"
								onClick={() => {
									this.setState({ visible: false })
									this.props.history.push(`/search?q=${value}`)
								}}
							/>
						</Menu.Item>
						{!this.props.authenticated ? (
							<Menu.Item>
								<Button
									color="green"
									content="Sign In"
									fluid
									icon="sign in"
									onClick={() => this.props.history.push("/signin?type=join")}
								/>
							</Menu.Item>
						) : (
							<Menu.Item onClick={this.onLogout}>Sign Out</Menu.Item>
						)}
						<Menu.Item
							name="activity"
							onClick={() => {
								this.props.history.push(`/assign`)
							}}
						>
							<b>Assign</b>
						</Menu.Item>
						{FallaciesSidebar}
					</Sidebar>
				</div>
			</Provider>
		)
	}
}

Header.defaultProps = {
	authenticated: false,
	logout
}

Header.propTypes = {
	authenticated: PropTypes.bool,
	logout: PropTypes.func
}

const mapStateToProps = (state, ownProps) => ({
	...state.user,
	...ownProps
})

export default connect(
	mapStateToProps,
	{ logout }
)(Header)
